const DivDropDownMenu = styled.div`
  position: absolute;
  top: 58px;
  width: 300px;
  transform: translateX(-45%);
  background-color: ${theme.bg};
  border: ${theme.border};
  border-radius: ${theme.border_radius};
  padding: 1rem;
  overflow: hidden;
  transition: height ${theme.speed} ease;
`;

const SubMenu = styled.div`
  width: 100%;
`;

export function DropdownMenu(){
  const [activeMenu, setActiveMenu] = useState('main');
  const [menuHeight, setMenuHeight] = useState(null);
  const dropdownRef = useRef(null);

  useEffect(() => {
    setMenuHeight(dropdownRef.current?.firstChild.offsetHeight + 32)
  }, [])

  function calcHeight(el) {
    const height = el.offsetHeight;
    setMenuHeight(height + 32);
  }

  function DropdownItem(props){
    return (
      <LinkMenuItem href="#" onClick={() => props.goToMenu && setActiveMenu(props.goToMenu)}>
        <SpanIconButton>{props.leftIcon}</SpanIconButton>
        {props.children}
      </LinkMenuItem>
    );
  }

  return (
    <DivDropDownMenu className="dropdown" style={{ height: menuHeight }} ref={dropdownRef}>

    <CSSTransition
      in={activeMenu === 'main'}
      timeout={500}
      classNames="menu-primary"
      unmountOnExit
      onEnter={calcHeight}>
      <SubMenu>
        <DropdownItem
          leftIcon={<RegionIcon />}
          goToMenu="region">
          Region
        </DropdownItem>
        <DropdownItem
          leftIcon={<LanguageIcon />}
          goToMenu="language">
          Language
        </DropdownItem>

      </SubMenu>
    </CSSTransition>

    <CSSTransition
      in={activeMenu === 'region'}
      timeout={500}
      classNames="menu-secondary"
      unmountOnExit
      onEnter={calcHeight}>
      <SubMenu>
        <DropdownItem goToMenu="main" leftIcon={<ArrowIcon />}>
          <h2>Region:</h2>
        </DropdownItem>
        <DropdownItem leftIcon={<AfricaIcon />}>Africa</DropdownItem>
        <DropdownItem leftIcon={<AmericaIcon />}>America</DropdownItem>
        <DropdownItem leftIcon={<AsiaIcon />}>Asia</DropdownItem>
        <DropdownItem leftIcon={<EuropeIcon />}>Europe</DropdownItem>
        <DropdownItem leftIcon={<OceaniaIcon />}>Oceania</DropdownItem>
      </SubMenu>
    </CSSTransition>

    <CSSTransition
      in={activeMenu === 'language'}
      timeout={500}
      classNames="menu-secondary"
      unmountOnExit
      onEnter={calcHeight}>
      <SubMenu>
        <DropdownItem goToMenu="main" leftIcon={<ArrowIcon />}>
          <h2>Language:</h2>
        </DropdownItem>
        <DropdownItem leftIcon={<EnglishIcon />}>English</DropdownItem>
        <DropdownItem leftIcon={<FrenchIcon />}>French</DropdownItem>
        <DropdownItem leftIcon={<SpanishIcon />}>Spanish</DropdownItem>
        <DropdownItem leftIcon={<GermanIcon />}>German</DropdownItem>
        <DropdownItem leftIcon={<PortuguesIcon />}>Portugues</DropdownItem>
        <DropdownItem leftIcon={<ItalianIcon />}>Italian</DropdownItem>
        <DropdownItem leftIcon={<RussianIcon />}>Russian</DropdownItem>
        <DropdownItem leftIcon={<ArabianIcon />}>Arabian</DropdownItem>
      </SubMenu>
    </CSSTransition>
  </DivDropDownMenu>
 );
}







